.logo {
	display: flex;
  justify-content: center;
  align-items: center;
  width: 12rem;
	font-size: 40px;
}

.logoPri {
	font-family: 'Amatic SC', cursive;
	color: #9c6644;
}

.logoSec {
	font-family: 'Dancing Script', cursive;
	color: #95D85A;
	margin-right: 0.6rem;
}

.icon {
	width: 2rem;
	height: auto;
}

.icon img {
	width: 100%;
	height: 100%;
	object-fit: contain;
}
